.quiz__variant-desc:before {
  content: "";
  position: absolute;
  top: -10px;
  left: 50%;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  box-shadow: 0 0 10px rgba(47, 47, 47, 0.2);
