.quiz__variant-desc:after {
  content: "";
  position: absolute;
  top: -10px;
  left: 50%;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  background-color: #ededed;
  transition: box-shadow 0.3s;
  box-shadow: 0 0 0 #6ca462 inset;
}

.quiz__input:checked + .quiz__variant-inner {
