  padding-bottom: 50px;
  background-color: #fff;
}
.reviews::after {
  content: "";
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translate(-50%, 15px);
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #fff;
}

.reviews__circle {
  position: absolute;
  z-index: 2;
  bottom: 100%;
  left: 50%;
  transform: translate(-50%, -9px);
  width: 55px;
  height: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: linear-gradient(rgba(108, 164, 98, 0.2) 0%, rgba(108, 164, 98, 0.45) 100%);
