  position: relative;
  height: 300px;
  background-color: beige;
  border-radius: 35px;
  overflow: hidden;
}
.reviews__item:before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.4);
}

.reviews__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.reviews__link {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90px;
  height: 90px;
  background: radial-gradient(50% 50% at 50% 50%, #96be8e 0, #6ca462 100%);
  opacity: 1;
  border-radius: 50%;
  box-shadow: 0 0 0 rgba(108, 164, 98, 0.4);
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(108, 164, 98, 0.9);
  }
  70% {
    box-shadow: 0 0 0 25px rgba(108, 164, 98, 0);
  }
  100% {
