.nav {
  margin-top: 0px;
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.nav__link {
  display: block;
  padding: 20px;
  text-transform: uppercase;
  font-size: 15px;
  font-weight: 600;
  transition: all 0.3s;
  opacity: 0.8;

  &:hover {
    opacity: 1;
  }
}